.dashboard-alerts {
  position: relative;
  padding: 1.5rem;
  background-color: #f9fafb;
  min-height: 100vh;
}
.dashboard-alerts .alert-system-header {
  margin-bottom: 2rem;
}
.dashboard-alerts .alert-system-header .system-title {
  font-size: 1.875rem;
  font-weight: 700;
  color: #111827;
  margin: 0 0 0.5rem 0;
}
.dashboard-alerts .alert-system-header .system-subtitle {
  color: #6b7280;
  font-size: 1.125rem;
  margin: 0;
}
.dashboard-alerts .summary-cards-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 3rem;
}
.dashboard-alerts .summary-cards-row .summary-card {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  align-items: center;
  gap: 1rem;
}
.dashboard-alerts .summary-cards-row .summary-card .card-icon {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}
.dashboard-alerts .summary-cards-row .summary-card .card-content .card-value {
  font-size: 1.875rem;
  font-weight: 700;
  color: #111827;
  line-height: 1;
  margin-bottom: 0.25rem;
}
.dashboard-alerts .summary-cards-row .summary-card .card-content .card-label {
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}
.dashboard-alerts .summary-cards-row .summary-card.active-alerts .card-icon {
  background-color: #fef3c7;
  color: #f59e0b;
}
.dashboard-alerts .summary-cards-row .summary-card.critical .card-icon {
  background-color: #fee2e2;
  color: #ef4444;
}
.dashboard-alerts .summary-cards-row .summary-card.acknowledged .card-icon {
  background-color: #d1fae5;
  color: #10b981;
}
.dashboard-alerts .summary-cards-row .summary-card.total-alerts .card-icon {
  background-color: #dbeafe;
  color: #3b82f6;
}
.dashboard-alerts .alert-management-section .section-header {
  margin-bottom: 1.5rem;
}
.dashboard-alerts .alert-management-section .section-header .section-title {
  font-size: 1.5rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.5rem 0;
}
.dashboard-alerts .alert-management-section .section-header .section-subtitle {
  color: #6b7280;
  font-size: 1rem;
  margin: 0;
}
.dashboard-alerts .alert-management-section .alert-tabs {
  display: flex;
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 0.25rem;
  margin-bottom: 1.5rem;
  overflow-x: auto;
}
.dashboard-alerts .alert-management-section .alert-tabs .tab-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  background: none;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
}
.dashboard-alerts .alert-management-section .alert-tabs .tab-button:hover {
  color: #374151;
  background-color: #f9fafb;
}
.dashboard-alerts .alert-management-section .alert-tabs .tab-button.active {
  background-color: #3b82f6;
  color: white;
}
.dashboard-alerts .alert-management-section .alert-tabs .tab-button .tab-count {
  font-size: 0.75rem;
  opacity: 0.8;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item {
  background-color: white;
  border-radius: 0.75rem;
  border: 1px solid #e5e7eb;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item:last-child {
  margin-bottom: 0;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item.critical {
  border-left: 4px solid #ef4444;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left {
  display: flex;
  align-items: flex-start;
  gap: 1rem;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-severity {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background-color: #fee2e2;
  border-radius: 0.375rem;
  flex-shrink: 0;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-severity svg {
  color: #ef4444;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-severity .severity-label {
  font-size: 0.75rem;
  font-weight: 600;
  color: #ef4444;
  text-transform: uppercase;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-content .alert-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #111827;
  margin: 0 0 0.5rem 0;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-content .alert-description {
  color: #6b7280;
  font-size: 0.875rem;
  margin: 0 0 0.75rem 0;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-content .alert-details {
  display: flex;
  gap: 1.5rem;
  font-size: 0.875rem;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-content .alert-details .stock-info {
  color: #ef4444;
  font-weight: 500;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-left .alert-content .alert-details .timestamp {
  color: #9ca3af;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions {
  display: flex;
  gap: 0.75rem;
  flex-shrink: 0;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions .action-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions .action-btn.acknowledge {
  background-color: #10b981;
  color: white;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions .action-btn.acknowledge:hover {
  background-color: #059669;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions .action-btn.dismiss {
  background-color: #6b7280;
  color: white;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions .action-btn.dismiss:hover {
  background-color: #4b5563;
}
.dashboard-alerts .alert-management-section .alert-list .alert-item .alert-actions .action-btn svg {
  color: currentColor;
}
.dashboard-alerts .fab {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  width: 3.5rem;
  height: 3.5rem;
  background-color: #3b82f6;
  color: white;
  border: none;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: all 0.2s;
}
.dashboard-alerts .fab:hover {
  background-color: #2563eb;
  transform: translateY(-2px);
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}
.dashboard-alerts .fab svg {
  color: white;
}
